  border-bottom: 1px dotted;
  text-decoration: none;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:active,
a:hover {
  outline-width: 0;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

code,
kbd,
pre,
