.l-base {
  width: inherit;
  height: auto !important;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  display: grid;
  grid-template: "header" minmax(auto, auto) "main" 1fr "footer" minmax(auto, auto)/100%;
}

